
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
components: {
Tabbar,
TabbarItem,
Group,
Cell,
XHeader,
Tab,
TabItem,
Swiper,
SwiperItem
},
created(){
this.getData();
},
data () {
return {
playerOptions : {
playbackRates: [0.7, 1.0, 1.5, 2.0], //
autoplay: false, //true,
muted: false, // 
loop: false, // 
preload: 'auto', // <video>auto,
language: 'zh-CN',
aspectRatio: '16:9', //  - "16:9""4:3"
fluid: true, // trueVideo.js player
sources: [{
type: "",
src: "" 
}],
poster: "", //

notSupportedMessage: '', //Video.js
controlBar: {
timeDivider: true,
durationDisplay: true,
remainingTimeDisplay: false,
fullscreenToggle: true  
}
},
mvList:[]
}
},
methods:{
getData(){
this.$api.get('top/mv?limit=15', null, r => {
console.log(r.data);
this.mvList = r.data;
});
}
}
}



